@keyframes dtm-anim-popin {
  0% {
    transform-origin: left;
    transform: scaleX(0.2);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  80% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

