@font-face {
    font-family: "opensensemap";
    src: url("../assets/fonts/osm/opensensemap.eot?d8dt7p");
    src: url("../assets/fonts/osm/opensensemap.eot?d8dt7p#iefix") format("embedded-opentype"),
        url("../assets/fonts/osm/opensensemap.ttf?d8dt7p") format("truetype"),
        url("../assets/fonts/osm/opensensemap.woff?d8dt7p") format("woff"),
        url("../assets/fonts/osm/opensensemap.svg?d8dt7p#opensensemap") format("svg");
    font-weight: normal;
    font-style: normal;
}

.osem-icon {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "opensensemap" !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

