.wbs-header {
  height: 48px;
  z-index: 9;
  background-color: #fff;
  border-bottom: 1px solid #ebebeb;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.wbs-header__nav {
  justify-content: left;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.wbs-header__nav__item {
  color: #525252;
  width: -webkit-min-content;
  width: min-content;
  font-weight: 450;
  text-decoration: none;
  transition: color .3s;
}

.wbs-header__nav__item--dropdown {
  width: auto;
}

.wbs-header__nav__item:hover {
  color: #000;
}

.wbs-header__nav__item--highlighted {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(45deg, #c70244 0%, #7308ca 100%);
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 600;
}

.wbs-header__nav__item--selected {
  color: #000;
  font-weight: 600;
}

.wbs-header__nav__item--selected:before {
  content: "";
  height: 2px;
  width: calc(100% + 4px);
  background: #000;
  display: block;
  position: absolute;
  bottom: 0;
  transform: translateX(-2px);
}

.wbs-datenschutz-page {
  color: #fff;
  height: 120vh;
}

.wbs-footer {
  min-height: 90px;
  background-color: #f5f5f5;
  border-top: 1px solid #e3e3e3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  padding: 10px 20px;
  font-size: .85rem;
  display: flex;
}

.wbs-footer__copyright {
  font-size: .7rem;
}

.wbs-footer__nav {
  flex-direction: row;
  align-items: center;
  gap: .5rem;
  display: flex;
}

@media (max-width: 767px) {
  .wbs-footer__nav {
    flex-direction: column;
    gap: .2rem;
  }
}

.wbs-footer__nav__item {
  color: #525252;
  font-weight: 450;
  text-decoration: none;
  transition: color .3s;
}

.wbs-footer__nav__item:hover {
  color: #000;
}

.wbs-footer__nav__item--selected {
  color: #000;
  font-weight: 600;
}

.sk-cube-grid {
  width: 100%;
  height: 100%;
  position: relative;
}

.sk-cube-grid__container {
  width: 40px;
  height: 40px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  float: left;
  background-color: #333;
  animation: sk-cubeGridScaleDelay 1.3s ease-in-out infinite;
}

.sk-cube-grid .sk-cube1 {
  animation-delay: .2s;
}

.sk-cube-grid .sk-cube2 {
  animation-delay: .3s;
}

.sk-cube-grid .sk-cube3 {
  animation-delay: .4s;
}

.sk-cube-grid .sk-cube4 {
  animation-delay: .1s;
}

.sk-cube-grid .sk-cube5 {
  animation-delay: .2s;
}

.sk-cube-grid .sk-cube6 {
  animation-delay: .3s;
}

.sk-cube-grid .sk-cube7 {
  animation-delay: 0s;
}

.sk-cube-grid .sk-cube8 {
  animation-delay: .1s;
}

.sk-cube-grid .sk-cube9 {
  animation-delay: .2s;
}

@keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    transform: scale3d(1, 1, 1);
  }

  35% {
    transform: scale3d(0, 0, 1);
  }
}

.spinner {
  width: 70px;
  text-align: center;
  margin: 100px auto 0;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #333;
  border-radius: 100%;
  animation: sk-bouncedelay 1.4s ease-in-out infinite both;
  display: inline-block;
}

.spinner .bounce1 {
  animation-delay: -.32s;
}

.spinner .bounce2 {
  animation-delay: -.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }

  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}

