@keyframes dtm-anim-popin {
  0% {
    transform-origin: 0;
    opacity: 0;
    transform: scaleX(.2);
  }

  15% {
    opacity: 1;
  }

  80% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale-up {
  0% {
    transform: scale(.9);
  }

  100% {
